.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: $side-menu-color;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;
  transition: $std-transition;

  &__top {
    margin-bottom: 24px;

    position: sticky;
    top: 0;
    z-index: 1;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    font-size: 16px;
    line-height: 27px;
    color: $main-color;
    text-decoration: none;
    transition: $std-transition;

    &:hover {
      color: $secondary-color;
    }
  }

  &__call-to-order {
    display: block;
    width: fit-content;

    padding-bottom: 7px;
    border-bottom: 1px solid $main-color;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: $main-color;
    text-decoration: none;
    transition: $std-transition;

    &:hover {
      color: $secondary-color;
      border-bottom: 1px solid $secondary-color;
    }
  }
}
